body {
  background-color: #0f0715;
  color: white;
  margin: 0;
}



.nav-menu ul {
  height: 50px;
  justify-content: flex-end;
  align-items: center;
  display: flex;
  list-style: none;
}

.nav-menu ul li a {
  display: block;
  margin: 40px 40px;
  color: #c4cfde;
  text-decoration: none;
  transition: transform 0.5s ease;
}

.nav-menu ul li a:hover {
  transform: scale(1.2);
  cursor: pointer;
  color: #8750f7;
}

